import PropTypes from "prop-types"
import React, { useState }  from "react"




import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from 'reactstrap';

const Header = (props) => {
 
const [isOpen, setIsOpen] = useState(false);
const toggle = () => setIsOpen(!isOpen);

return (
  <div>
    <Navbar className="navbarrenk" fixed="top" light expand="sm">
      <div className="container" >
      <NavbarBrand className="font" href="/">{props.siteTitle}</NavbarBrand>
      <NavbarToggler onClick={toggle} />
      <Collapse isOpen={isOpen} navbar>
        <Nav className="mr-auto" navbar>
          <NavItem>
            <NavLink href="/about">İletişim</NavLink>
          </NavItem>        
            <NavItem>
            <NavLink href="/tags">Kategoriler</NavLink>
          </NavItem>

        </Nav>
      </Collapse>
      </div>
    </Navbar>
  </div>
);
}


    

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header

