import React from 'react'
import { Card, CardTitle, CardBody, Form, FormGroup } from 'reactstrap'
import Img from 'gatsby-image'
import {graphql, StaticQuery, Link} from 'gatsby'
import Search from "./search"





const Sidebar = ({data, location}) => {


    return(
 <div>

     <Card>
         <CardBody>
         <CardTitle className="text-center">Reklam</CardTitle>
            <img src="https://via.placeholder.com/320x200" alt="Advert" style={{ width: "100%" }} />
         </CardBody>
     </Card>
     <StaticQuery query={sidebarQuery} render={(data) => (
                 <div>
       <Card>
         <CardBody>
             <CardTitle className="text-center mb-3">
             </CardTitle>
             <Form className="text-center">
                 <FormGroup>
                    <Search searchIndex={data.siteSearchIndex.index}/>
                 </FormGroup>
             </Form>
         </CardBody>
     </Card>
     <CardTitle>
            <h3 className="text-center">Son Eklenen Yazılar</h3>
     </CardTitle>
     {data.allMarkdownRemark.edges.map(({node}) => (
                                              
                         <Card key={node.id}>
                             <Link  to={node.fields.slug}>
                                 <Img className="card-image-top" fluid={node.frontmatter.featuredImage.childImageSharp.fluid} />
                             </Link>
                             <CardBody>
                                 <CardTitle className="text-center">
                                     <Link className="sidebarTitle"
                                          to={node.fields.slug}>
                                         {node.frontmatter.title}
                                     </Link>
                                 </CardTitle>
                             </CardBody>
                         </Card>
                     ))}
                 </div>
             )} />

         </div>
    )
        }

const sidebarQuery = graphql`
    query sidebarQueryAndSearchIndexQuery {
        allMarkdownRemark(
            sort: {fields: [frontmatter___date], order: DESC}
            limit: 3
        ) {
            edges{
                node{
                    id
                    frontmatter{
                        title
                        tags
                        featuredImage{
                            childImageSharp{
                                fluid(maxWidth: 300){
                                    ...GatsbyImageSharpFluid
                                }
                            }
                        }
                    }
                    fields{
                        slug
                    }
                }
            }
        }
        siteSearchIndex {
            index
          }
    }
`

export default Sidebar

