import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"
import { Row, Col } from "reactstrap"

import Header from "./header"
import Sidebar from "./sidebar"
import "../styles/index.scss"

const Layout = ({ children, pageTitle }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
<script src="https://kit.fontawesome.com/addac6c835.js" crossOrigin="anonymous"></script>
      <Header siteTitle={data.site.siteMetadata.title} />
      <div className="container" id="content" 
            >
              <h1 className="bosluk">{pageTitle}</h1>
              <Row>
                <Col lg="8" md="9" xs="12">{children}</Col>
                <Col lg="4" md="3"><Sidebar/></Col>
              </Row>
        <footer>
          © {new Date().getFullYear()}, Built with
          {` `}
          <a href="https://www.linkedin.com/in/kevser-%C3%B6zkan-3548021a7/">KevserOzkan</a>
        </footer>
      </div>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
